import "./styles.css";
import axios from "axios";
import { useState } from "react";
import Recipe from "./Recipe";

function App() {
  const [query, setQuery] = useState("");
  const [recipes, setRecipes] = useState([]);
  const [healthLabel, setHealthLabel] = useState([]);

  var url = `https://api.edamam.com/search?q=${query}&app_id=7a6c0141&app_key=c368ab35b276b590ad972cb36d06c183&Health=${healthLabel}`;

  async function getRecipes() {
    var result = await axios.get(url);
    setRecipes(result.data.hits);
    console.log(result.data);
  }

  const onSubmit = (e) => {
    e.preventDefault();
    getRecipes();
  };

  return (
    <div className="app">
      <h1>
        Ghar Ka Food Plaza Hotel Search <span>üçî</span>
      </h1>
      <form className="app__searchForm" onSubmit={onSubmit}>
        <input
          className="app__input"
          type="text"
          placeholder="enter ingridient"
          autoComplete="Off"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
        />
        <input className="app__submit" type="submit" value="Search" />
        <select className="app__healdthLabels">
          <option
            onClick={() => {
              setHealthLabel("Vegan");
            }}
          >
            Vegan
          </option>
          <option
            onClick={() => {
              setHealthLabel("Vegetarian");
            }}
          >
            Vegetarian
          </option>
          <option
            onClick={() => {
              setHealthLabel("dairy-free");
            }}
          >
            dairy-free
          </option>
          <option
            onClick={() => {
              setHealthLabel("Gluten");
            }}
          >
            Gluten
          </option>
          <option
            onClick={() => {
              setHealthLabel("Low-sugar");
            }}
          >
            Low-sugar
          </option>
        </select>
      </form>

      <div className="app__recipes">
        {recipes.map((recipe) => {
          return <Recipe recipe={recipe} />;
        })}
      </div>
    </div>
  );
}

export default App;
